/* Bright Blue & Light Yellow Design System */
:root {
  /* Primary Bright Colors */
  --bright-blue: #007BFF;
  --light-blue: #87CEEB;
  --electric-blue: #00BFFF;
  --sky-blue: #87CEFA;
  --light-yellow: #FFF8DC;
  --bright-yellow: #FFD700;
  --sunshine-yellow: #FFFF99;
  --warm-yellow: #FFE4B5;
  
  /* Neutral Colors for Balance */
  --soft-white: #FFFFFF;
  --warm-gray: #F8F9FA;
  --gentle-gray: #E9ECEF;
  --text-dark: #1E3A8A;
  --text-light: #64748B;
}

body {
  margin: 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif !important;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fef9c3; /* yellow-100 */
  color: var(--text-dark);
  line-height: 1.6;
}

/* Hide scrollbar on mobile for CreateYours page */
@media (max-width: 767px) {
  body::-webkit-scrollbar,
  html::-webkit-scrollbar {
    display: none;
    width: 0;
    height: 0;
  }
  
  body {
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
  }
  
  html {
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
  }
}

/* Hide scrollbar in canvas area */
.happy-card::-webkit-scrollbar,
.happy-card canvas::-webkit-scrollbar,
canvas::-webkit-scrollbar {
  display: none;
  width: 0;
  height: 0;
}

.happy-card {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

canvas {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

code {
  font-family: 'JetBrains Mono', 'Fira Code', source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* PerfectGiftSection background styles */
.perfect-gift-bg {
  background-position: center;
  background-size: cover;
}

/* Fixed background attachment for medium screens and above */
@media (min-width: 768px) {
  .perfect-gift-bg {
    background-attachment: fixed;
  }
}

/* Custom Font Classes */
.lazy-dog-title {
  font-family: 'Fredoka One', cursive !important;
  font-weight: 400;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-size: 1.2em;
