/* Global Text Elements - Apply Inter to all text */
p, span, div, a, button, input, textarea, label, li, td, th {
  font-family: 'Inter', sans-serif !important;
  font-weight: 300;
  letter-spacing: 0.3px;
}

/* Specific element overrides */
h1, h2, h3, h4, h5, h6 {
